.component-display {
  background-color: #858694;
  color: white;
  text-align: right;
  font-weight: 200;
  width: 100%;
  -ms-flex-direction: row;
      flex-direction: row;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.component-display > p {
  font-weight: bold;
  margin-left: 0.5rem;
}

.component-display > div {
  font-size: 2.5rem;
  padding: 0.2rem 0.7rem 0.1rem 0.5rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

@-webkit-keyframes blink {
  0% { background: rgba(255, 255, 255, 0.5); }
  50% { background: rgba(255, 255, 255, 0); }
  100% { background: rgba(255, 255, 255, 0.5); }
}

@keyframes blink {
  0% { background: rgba(255, 255, 255, 0.5); }
  50% { background: rgba(255, 255, 255, 0); }
  100% { background: rgba(255, 255, 255, 0.5); }
}

.component-display > div > div {
  height: 80%;
  width: 0.2ex;
  margin: auto 0 auto 0.3ex;
}

.cursor {
  background-color: rgba(255, 255, 255, 1);
  -webkit-animation: blink 1s infinite;
          animation: blink 1s infinite;
}



